+(function($) {
  'use strict'

  $(function() {
    /*  */
    BJUI.setRegional('alertmsg', {
      title: { error: '', info: '', warn: '', correct: '', confirm: '' },
      btnMsg: { ok: '', yes: '', no: '', cancel: '' }
    })

    /* dialog */
    BJUI.setRegional('dialog', {
      close: '',
      maximize: '',
      restore: '',
      minimize: '',
      title: ''
    })

    /* order by */
    BJUI.setRegional('orderby', {
      asc: '',
      desc: ''
    })

    /*  */
    BJUI.setRegional('pagination', {
      total: '/',
      first: '',
      last: '',
      prev: '',
      next: '',
      jumpto: '',
      jump: '',
      page: '',
      refresh: ''
    })

    BJUI.setRegional('datagrid', {
      asc: '',
      desc: '',
      showhide: '/ ',
      filter: '',
      clear: '',
      lock: '',
      unlock: '',
      add: '',
      edit: '',
      save: '',
      update: '',
      cancel: '',
      del: '',
      prev: '',
      next: '',
      refresh: '',
      query: '',
      'import': '',
      'export': '',
      all: '',
      'true': '',
      'false': '',
      selectMsg: '',
      editMsg: '',
      saveMsg: '',
      delMsg: '',
      delMsgM: ''
    })

    /* ajax */
    BJUI.setRegional('progressmsg', '...')

    /*  */
    BJUI.setRegional('datepicker', {
      close: '',
      prev: '',
      next: '',
      clear: '',
      ok: '',
      dayNames: ['', '', '', '', '', '', ''],
      monthNames: ['', '', '', '', '', '', '', '', '', '', '', '']
    })

    /* navtab  */
    BJUI.setRegional('navtabCM', {
      refresh: '',
      close: '',
      closeother: '',
      closeall: ''
    })

    /* dialog */
    BJUI.setRegional('dialogCM', {
      refresh: '',
      close: '',
      closeother: '',
      closeall: ''
    })

    /* upload */
    BJUI.setRegional('upload', {
      upConfirm: '',
      upPause: '',
      upCancel: ''
    })

    /* 503 */
    BJUI.setRegional('statusCode_503', '')

    /* timeout */
    BJUI.setRegional('sessiontimeout', '')

    /*  */
    BJUI.setRegional('plhmsg', '')

    /*  */
    BJUI.setRegional('nocheckgroup', '["data-group"]')

    /*  */
    BJUI.setRegional('notchecked', '')

    /*  */
    BJUI.setRegional('selectmsg', '')

    /*  */
    BJUI.setRegional('validatemsg', ' [{0}] ')

    /*  */
    BJUI.setRegional('uititle', 'B-JUI')

    /* navtab */
    BJUI.setRegional('maintab', '')

    /**
     *
     *  Plugins regional setting
     */
    /* nice validate - Global configuration */
    $.validator && $.validator.config({
      // stopOnError: false,
      // theme: 'yellow_right',
      defaultMsg: '{0}',
      loadingMsg: '...',

      // Custom rules
      rules: {
        digits: [/^\d+$/, ''],
        number: [/^[\-\+]?((([0-9]{1,3})([,][0-9]{3})*)|([0-9]+))?([\.]([0-9]+))?$/, ''],
        letters: [/^[a-z]+$/i, '{0}'],
        datetime: [/^\d{4}[\/\-](0?[1-9]|1[012])[\/\-](0?[1-9]|[12][0-9]|3[01])\s+(2[0123]|(1|0?)[0-9]){1}:([0-5][0-9]){1}:([0-5][0-9]){1}$/,
          'yyyy-MM-dd HH:mm:ss'],
        pattern: function(element, params) {
          if (!params) return true

          var date = element.value.parseDate(params)

          return (!date ? this.renderMsg('', params) : true)
        },
        accept: function(element, params) {
          if (!params) return true

          var ext = params[0]

          return (ext === '*') ||
                           (new RegExp('.(?:' + (ext || 'png|jpg|jpeg|gif') + ')$', 'i')).test(element.value) ||
                           this.renderMsg('{1}', ext.replace('|', ','))
        }

      }
    })
  })
}(jQuery))
